* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

header {
  padding: 16px 32px;
  text-align: left;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1), 0 2px 2px rgba(0, 0, 0, 0.15);
}

footer {
  width: 720px;
  margin: 0 auto;
}

.card {
  width: 200px;
  margin: 8px 16px;
  padding: 16px;
  background: #f6f6f6;
  border-radius: 4px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1), 0 8px 8px rgba(0, 0, 0, 0.15);
}

.container {
  max-width: 980px;
  margin: 64px auto;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
}

h2.count {
  padding-top: 16px;
}

.stats-container {
  margin-bottom: 16px;
}

.stats-title {
  text-align: left;
  font-size: 24px;
  padding: 8px 16px 0 16px;
}
